"use client";

import React, { useState, useEffect } from "react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Settings, CheckCircle, UserCircle, Save, Upload, Lock, MapPin, Phone, Mail, User } from "lucide-react";
import { Montserrat } from "next/font/google";

// Configuration de la police Montserrat
const montserrat = Montserrat({
  subsets: ["latin"],
  weight: ["400", "600", "800"],
  display: "swap",
});

export default function AdminSettings() {
  const [formData, setFormData] = useState({
    firstName: "Admin",
    lastName: "Utilisateur",
    email: "admin@example.com",
    phone: "",
    address: {
      street: "",
      city: "",
      province: "",
    },
    password: "",
    confirmPassword: "",
    profileImage: null,
  });

  const [imagePreview, setImagePreview] = useState(null);
  const [successMessage, setSuccessMessage] = useState(null);
  const [provinceFilter, setProvinceFilter] = useState("");
  const [activeTab, setActiveTab] = useState("profile");
  const [isDragOver, setIsDragOver] = useState(false);

  // Animation de scroll pour les sections
  const scrollToSection = (section) => {
    const element = document.getElementById(section);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
    setActiveTab(section);
  };

  // Liste des 58 wilayas d'Algérie
  const provinces = [
    "Adrar", "Chlef", "Laghouat", "Oum El Bouaghi", "Batna", "Béjaïa", "Biskra", "Béchar", "Blida", "Bouira",
    "Tamanrasset", "Tébessa", "Tlemcen", "Tiaret", "Tizi Ouzou", "Alger", "Djelfa", "Jijel", "Sétif", "Saïda",
    "Skikda", "Sidi Bel Abbès", "Annaba", "Guelma", "Constantine", "Médéa", "Mostaganem", "M'Sila", "Mascara",
    "Ouargla", "Oran", "El Bayadh", "Illizi", "Bordj Bou Arréridj", "Boumerdès", "El Tarf", "Tindouf",
    "Tissemsilt", "El Oued", "Khenchela", "Souk Ahras", "Tipaza", "Mila", "Aïn Defla", "Naâma",
    "Aïn Témouchent", "Ghardaïa", "Relizane", "Timimoun", "Bordj Badji Mokhtar", "Ouled Djellal",
    "Béni Abbès", "In Salah", "In Guezzam", "Touggourt", "Djanet", "El M'Ghair", "El Menia"
  ];

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    
    if (name === "profileImage" && files && files[0]) {
      const file = files[0];
      setFormData((prev) => ({ ...prev, profileImage: file }));
      setImagePreview(URL.createObjectURL(file));
    } else if (["street", "city", "province"].includes(name)) {
      setFormData((prev) => ({
        ...prev,
        address: { ...prev.address, [name]: value },
      }));
      if (name === "province") setProvinceFilter(value);
    } else {
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (formData.password && formData.password !== formData.confirmPassword) {
      showNotification("Les mots de passe ne correspondent pas !", "error");
      return;
    }
    
    showNotification("Informations mises à jour avec succès !", "success");
  };

  const showNotification = (message, type = "success") => {
    setSuccessMessage({ message, type });
    setTimeout(() => setSuccessMessage(null), 3000);
  };

  const handleDragOver = (e) => {
    e.preventDefault();
    setIsDragOver(true);
  };

  const handleDragLeave = () => {
    setIsDragOver(false);
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setIsDragOver(false);
    
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      const file = e.dataTransfer.files[0];
      if (file.type.startsWith('image/')) {
        setFormData((prev) => ({ ...prev, profileImage: file }));
        setImagePreview(URL.createObjectURL(file));
      } else {
        showNotification("Veuillez déposer uniquement des images", "error");
      }
    }
  };

  // Filtrer les provinces
  const filteredProvinces = provinces.filter((province) =>
    province.toLowerCase().includes(provinceFilter.toLowerCase())
  );

  // Effet de parallaxe sur le scrolling
  useEffect(() => {
    const handleScroll = () => {
      const sections = ["profile", "contact", "address", "security"];
      
      for (const section of sections) {
        const element = document.getElementById(section);
        if (!element) continue;
        
        const rect = element.getBoundingClientRect();
        const isInView = rect.top <= 200 && rect.bottom >= 200;
        
        if (isInView) {
          setActiveTab(section);
          break;
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-gray-50 to-purple-50 py-12 px-4 sm:px-6 lg:px-8">
      {/* Formes décoratives en arrière-plan */}
      <div className="fixed inset-0 overflow-hidden z-0 pointer-events-none">
        <div className="absolute -top-32 -right-32 w-96 h-96 bg-purple-100 rounded-full opacity-50 blur-3xl"></div>
        <div className="absolute top-1/4 -left-48 w-96 h-96 bg-blue-100 rounded-full opacity-50 blur-3xl"></div>
        <div className="absolute bottom-0 right-1/3 w-64 h-64 bg-green-100 rounded-full opacity-40 blur-3xl"></div>
      </div>

      {/* Navigation latérale fixe */}
      <div className="fixed left-4 top-1/2 transform -translate-y-1/2 z-10 hidden lg:block">
        <div className="bg-white/80 backdrop-blur-md rounded-2xl shadow-lg p-4 space-y-6">
          <button 
            onClick={() => scrollToSection("profile")}
            className={`flex items-center space-x-2 p-2 rounded-lg transition-all w-full ${activeTab === "profile" ? "bg-black text-white" : "hover:bg-gray-100"}`}
          >
            <User size={18} />
            <span>Profil</span>
          </button>
          <button 
            onClick={() => scrollToSection("contact")}
            className={`flex items-center space-x-2 p-2 rounded-lg transition-all w-full ${activeTab === "contact" ? "bg-black text-white" : "hover:bg-gray-100"}`}
          >
            <Mail size={18} />
            <span>Contact</span>
          </button>
          <button 
            onClick={() => scrollToSection("address")}
            className={`flex items-center space-x-2 p-2 rounded-lg transition-all w-full ${activeTab === "address" ? "bg-black text-white" : "hover:bg-gray-100"}`}
          >
            <MapPin size={18} />
            <span>Adresse</span>
          </button>
          <button 
            onClick={() => scrollToSection("security")}
            className={`flex items-center space-x-2 p-2 rounded-lg transition-all w-full ${activeTab === "security" ? "bg-black text-white" : "hover:bg-gray-100"}`}
          >
            <Lock size={18} />
            <span>Sécurité</span>
          </button>
        </div>
      </div>

      {/* Contenu principal */}
      <div className="max-w-5xl mx-auto relative z-1">
        {/* En-tête avec animation */}
        <div className="mb-10 relative overflow-hidden rounded-3xl shadow-2xl">
          <div className="absolute inset-0 bg-gradient-to-r from-indigo-500 via-purple-500 to-pink-500 opacity-90"></div>
          <div className="relative z-10 px-8 py-12 text-white">
            <div className="flex items-center gap-4 mb-6">
              <div className="p-3 bg-white/20 backdrop-blur-md rounded-xl">
                <Settings className="h-8 w-8 text-white" />
              </div>
              <h1
                className={`text-4xl font-extrabold tracking-tight ${montserrat.className}`}
              >
                Paramètres Administrateur
              </h1>
            </div>
            <p className="text-white/80 max-w-2xl text-lg">
              Personnalisez votre profil et gérez vos informations personnelles avec style et sécurité
            </p>
            
            {/* Navigation rapide pour mobile */}
            <div className="mt-8 flex overflow-x-auto gap-2 md:hidden pb-2">
              <button 
                onClick={() => scrollToSection("profile")}
                className={`flex items-center space-x-2 py-2 px-4 rounded-lg whitespace-nowrap ${activeTab === "profile" ? "bg-white text-purple-700 font-medium" : "bg-white/20"}`}
              >
                <User size={16} />
                <span>Profil</span>
              </button>
              <button 
                onClick={() => scrollToSection("contact")}
                className={`flex items-center space-x-2 py-2 px-4 rounded-lg whitespace-nowrap ${activeTab === "contact" ? "bg-white text-purple-700 font-medium" : "bg-white/20"}`}
              >
                <Mail size={16} />
                <span>Contact</span>
              </button>
              <button 
                onClick={() => scrollToSection("address")}
                className={`flex items-center space-x-2 py-2 px-4 rounded-lg whitespace-nowrap ${activeTab === "address" ? "bg-white text-purple-700 font-medium" : "bg-white/20"}`}
              >
                <MapPin size={16} />
                <span>Adresse</span>
              </button>
              <button 
                onClick={() => scrollToSection("security")}
                className={`flex items-center space-x-2 py-2 px-4 rounded-lg whitespace-nowrap ${activeTab === "security" ? "bg-white text-purple-700 font-medium" : "bg-white/20"}`}
              >
                <Lock size={16} />
                <span>Sécurité</span>
              </button>
            </div>
          </div>
          
          {/* Vagues d'animation en bas de l'en-tête */}
          <div className="absolute bottom-0 left-0 right-0">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 120" className="w-full h-12 text-white">
              <path fill="currentColor" fillOpacity="1" d="M0,64L60,80C120,96,240,128,360,128C480,128,600,96,720,80C840,64,960,64,1080,64C1200,64,1320,64,1380,64L1440,64L1440,0L1380,0C1320,0,1200,0,1080,0C960,0,840,0,720,0C600,0,480,0,360,0C240,0,120,0,60,0L0,0Z"></path>
            </svg>
          </div>
        </div>

        {/* Message de notification */}
        {successMessage && (
          <div className="fixed top-4 right-4 z-50">
            <div className={`px-6 py-3 rounded-lg shadow-2xl flex items-center gap-3 transform transition-all duration-500 ease-in-out animate-bounce-once ${
              successMessage.type === "success" ? "bg-green-600 text-white" : "bg-red-600 text-white"
            }`}>
              <CheckCircle className="h-6 w-6" />
              <span className="font-semibold">{successMessage.message}</span>
              <button
                onClick={() => setSuccessMessage(null)}
                className="text-white hover:text-gray-200 transition-colors duration-200"
              >
                ✕
              </button>
            </div>
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-8">
          {/* Section Profil */}
          <Card id="profile" className="bg-white/80 backdrop-blur-lg border-0 rounded-3xl shadow-xl overflow-hidden transition-all duration-300 hover:shadow-2xl">
            <CardHeader className="border-b border-gray-100 bg-gradient-to-r from-gray-50 to-white">
              <div className="flex items-center gap-3">
                <User className="h-6 w-6 text-indigo-600" />
                <h2
                  className={`text-2xl font-bold text-gray-800 ${montserrat.className}`}
                >
                  Informations de Profil
                </h2>
              </div>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="flex flex-col md:flex-row gap-8 items-center md:items-start">
                {/* Photo de profil avec zone de drop */}
                <div className="relative group">
                  <div 
                    className={`w-40 h-40 rounded-full overflow-hidden ${isDragOver ? 'ring-4 ring-indigo-500 ring-offset-2' : 'ring-2 ring-gray-200'} transition-all duration-300`}
                    onDragOver={handleDragOver}
                    onDragLeave={handleDragLeave}
                    onDrop={handleDrop}
                  >
                    {imagePreview ? (
                      <img
                        src={imagePreview}
                        alt="Aperçu de la photo de profil"
                        className="w-full h-full object-cover"
                      />
                    ) : (
                      <div className="w-full h-full flex items-center justify-center bg-gray-100">
                        <UserCircle className="w-24 h-24 text-gray-400" />
                      </div>
                    )}
                    
                    {/* Overlay au survol */}
                    <div className="absolute inset-0 bg-black/50 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex items-center justify-center">
                      <Upload className="text-white h-8 w-8" />
                    </div>
                  </div>
                  
                  <input
                    type="file"
                    id="profileImage"
                    name="profileImage"
                    accept="image/*"
                    onChange={handleChange}
                    className="hidden"
                  />
                  
                  <label
                    htmlFor="profileImage"
                    className="mt-4 cursor-pointer bg-white text-indigo-600 border border-indigo-200 px-4 py-2 rounded-lg hover:bg-indigo-50 transition-all duration-200 shadow-sm flex items-center justify-center gap-2 text-sm font-medium"
                  >
                    <Upload size={16} />
                    Changer la photo
                  </label>
                  <p className="text-xs text-gray-500 mt-2 text-center">
                    Ou glissez-déposez une image
                  </p>
                </div>

                {/* Nom et Prénom */}
                <div className="flex-1 w-full">
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div className="space-y-2">
                      <label htmlFor="firstName" className="block text-sm font-medium text-gray-700">
                        Prénom
                      </label>
                      <input
                        type="text"
                        id="firstName"
                        name="firstName"
                        value={formData.firstName}
                        onChange={handleChange}
                        className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                        placeholder="Votre prénom"
                      />
                    </div>
                    <div className="space-y-2">
                      <label htmlFor="lastName" className="block text-sm font-medium text-gray-700">
                        Nom
                      </label>
                      <input
                        type="text"
                        id="lastName"
                        name="lastName"
                        value={formData.lastName}
                        onChange={handleChange}
                        className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                        placeholder="Votre nom"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Section Contact */}
          <Card id="contact" className="bg-white/80 backdrop-blur-lg border-0 rounded-3xl shadow-xl overflow-hidden transition-all duration-300 hover:shadow-2xl">
            <CardHeader className="border-b border-gray-100 bg-gradient-to-r from-gray-50 to-white">
              <div className="flex items-center gap-3">
                <Mail className="h-6 w-6 text-indigo-600" />
                <h2
                  className={`text-2xl font-bold text-gray-800 ${montserrat.className}`}
                >
                  Informations de Contact
                </h2>
              </div>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                    Email
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Mail size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      className="w-full pl-10 pr-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                      placeholder="votre.email@example.com"
                    />
                  </div>
                </div>
                <div className="space-y-2">
                  <label htmlFor="phone" className="block text-sm font-medium text-gray-700">
                    Numéro de téléphone
                  </label>
                  <div className="relative">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                      <Phone size={18} className="text-gray-400" />
                    </div>
                    <input
                      type="tel"
                      id="phone"
                      name="phone"
                      value={formData.phone}
                      onChange={handleChange}
                      className="w-full pl-10 pr-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                      placeholder="+213 12 345 678"
                    />
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Section Adresse */}
          <Card id="address" className="bg-white/80 backdrop-blur-lg border-0 rounded-3xl shadow-xl overflow-hidden transition-all duration-300 hover:shadow-2xl">
            <CardHeader className="border-b border-gray-100 bg-gradient-to-r from-gray-50 to-white">
              <div className="flex items-center gap-3">
                <MapPin className="h-6 w-6 text-indigo-600" />
                <h2
                  className={`text-2xl font-bold text-gray-800 ${montserrat.className}`}
                >
                  Adresse
                </h2>
              </div>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="space-y-6">
                <div>
                  <label htmlFor="street" className="block text-sm font-medium text-gray-700 mb-1">
                    Rue
                  </label>
                  <input
                    type="text"
                    id="street"
                    name="street"
                    value={formData.address.street}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                    placeholder="Nom de la rue"
                  />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label htmlFor="city" className="block text-sm font-medium text-gray-700 mb-1">
                      Ville
                    </label>
                    <input
                      type="text"
                      id="city"
                      name="city"
                      value={formData.address.city}
                      onChange={handleChange}
                      className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                      placeholder="Ville"
                    />
                  </div>
                  <div>
                    <label htmlFor="province" className="block text-sm font-medium text-gray-700 mb-1">
                      Province
                    </label>
                    <div className="relative">
                      <input
                        type="text"
                        id="province"
                        name="province"
                        value={formData.address.province}
                        onChange={handleChange}
                        list="provinces-list"
                        className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                        placeholder="Saisir ou sélectionner une province"
                      />
                      <datalist id="provinces-list">
                        {filteredProvinces.map((province) => (
                          <option key={province} value={province} />
                        ))}
                      </datalist>
                    </div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Section Sécurité */}
          <Card id="security" className="bg-white/80 backdrop-blur-lg border-0 rounded-3xl shadow-xl overflow-hidden transition-all duration-300 hover:shadow-2xl">
            <CardHeader className="border-b border-gray-100 bg-gradient-to-r from-gray-50 to-white">
              <div className="flex items-center gap-3">
                <Lock className="h-6 w-6 text-indigo-600" />
                <h2
                  className={`text-2xl font-bold text-gray-800 ${montserrat.className}`}
                >
                  Sécurité
                </h2>
              </div>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="space-y-2">
                  <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                    Nouveau mot de passe
                  </label>
                  <input
                    type="password"
                    id="password"
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                    placeholder="••••••••"
                  />
                  <p className="text-xs text-gray-500">
                    Utilisez au moins 8 caractères avec des lettres et des chiffres
                  </p>
                </div>
                <div className="space-y-2">
                  <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                    Confirmer le mot de passe
                  </label>
                  <input
                    type="password"
                    id="confirmPassword"
                    name="confirmPassword"
                    value={formData.confirmPassword}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-white border border-gray-200 rounded-xl focus:ring-2 focus:ring-indigo-600 focus:border-indigo-600 transition-all duration-200 text-gray-800 placeholder-gray-400 shadow-sm"
                    placeholder="••••••••"
                  />
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Bouton flottant de sauvegarde */}
          <div className="fixed bottom-8 right-8 z-20">
            <Button
              type="submit"
              className="group bg-gradient-to-r from-indigo-600 to-purple-600 text-white px-6 py-3 rounded-full hover:shadow-lg hover:from-indigo-700 hover:to-purple-700 transition-all duration-300 shadow-md flex items-center gap-2"
            >
              <Save className="h-5 w-5 group-hover:animate-pulse" />
              <span>Sauvegarder</span>
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
}